@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css2?family=Grechen+Fuemen&display=swap);
.search{width:100%;position:relative;display:flex}.searchTerm{width:100%;border:3px solid #fff;border-right:none;padding:20px;height:50px;border-radius:30px;outline:none;color:#fff;background-color:rgba(220,20,60,0)}.searchTerm:focus{color:#fff}.searchButton{width:52px;height:50px;border:1px solid #ffffff99;background:#f1f4f500;text-align:center;color:#fff;border-radius:50%;cursor:pointer;font-size:25px}.filter{color:#fff;display:flex;justify-content:space-evenly;font-size:large}.filter label{display:flex;cursor:pointer;font-weight:500;position:relative;overflow:hidden;margin-bottom:.375em}.filter label input{position:absolute;left:-9999px}.filter label input:checked+span{background-color:rgba(255,117,142,.7137254902)}.filter label input:checked+span:before{box-shadow:inset 0 0 0 .4375em #781381}.filter label span{display:flex;align-items:center;padding:.375em .75em .375em .375em;border-radius:99em;transition:.25s ease}.filter label span:hover{background-color:#e9d9eb;color:#3d0670}.filter label span:before{display:flex;flex-shrink:0;content:"";background-color:#fff;width:1.5em;height:1.5em;border-radius:50%;margin-right:.375em;transition:.25s ease;box-shadow:inset 0 0 0 .125em #781381}
.card{width:280px;height:360px;border-radius:15px;padding:1.5rem;background:#fff;position:relative;display:flex;align-items:center;transition:.4s ease-out;box-shadow:0px 7px 10px rgba(0,0,0,.5)}.card:hover{-webkit-transform:translateY(10px);transform:translateY(10px)}.card:hover:before{opacity:1}.card:hover .info{opacity:1;-webkit-transform:translateY(0px);transform:translateY(0px)}.card:before{content:"";position:absolute;top:0;left:0;display:block;width:100%;height:100%;border-radius:15px;background:rgba(0,0,0,.6);z-index:2;transition:.5s;opacity:0}.card img{width:100%;height:100%;object-fit:cover;position:absolute;top:0;left:0;border-radius:15px}.card .info{position:relative;z-index:3;color:#fff;opacity:0;-webkit-transform:translateY(30px);transform:translateY(30px);transition:.5s}.card .info h5{text-align:center;margin:0px}.card .info .list-group-item{color:beige;background-color:rgba(255,0,0,0);letter-spacing:1px;font-size:15px;margin-top:8px}
.navbar-brand {
  font-family: "Grechen Fuemen", cursive;
  font-size: 50px;
  text-shadow: -1px 3px 4px #df12cc;
}

.navbar-nav {
  margin-left: auto;
}
.nav-item {
  margin-right: 2rem;
  color: #ffccd5;
}
.btn {
  text-decoration: none;
  color: #ffccd5;
  font-size: 18px;
}
.btn:hover {
  color: #f05975;
}
.active {
  color: #f05975;
}
.btn:focus {
  box-shadow: none;
}

h4{
    margin-top: 5%;
    color: blanchedalmond;
    text-align: center;
}
body {
  background-image: linear-gradient(
    to bottom,
    #10002b,
    #140737,
    #1a0b43,
    #230c50,
    #2d0c5c,
    #3c156d,
    #4c1e7e,
    #5d278f,
    #773caa,
    #9151c6,
    #ac67e2,
    #c77dff
  );
  background-repeat: no-repeat;
  min-height: 100vh;
}

